.cart-info {
  display: flex;
  align-items: center;
}

.cart-image {
  width: 50px;
  height: 50px;
  border-radius: 8px;
  margin-right: 10px;
}

.cart-details {
  display: flex;
  flex-direction: column;
}

.cart-details h4 {
  margin: 0;
  font-size: 16px;
  font-weight: bold;
  color: #333;
}

.view-menu {
  margin: 0;
  font-size: 14px;
  color: #3b3b3b;
  cursor: pointer;
  display: -webkit-box;
  -webkit-line-clamp: 1; /* Limit the text to 2 lines */
  -webkit-box-orient: vertical;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: normal;
  /* width: 90%; */
}

/* Actions */
.cart-actions {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.view-cart-btn {
  background: #009944;
  color: white;
  border: none;
  padding: 8px 12px;
  border-radius: 5px;
  font-size: 14px;
  cursor: pointer;
}

.view-cart-btn:hover {
  background: #009944;
}

.item-count {
  margin: 5px 0 0;
  font-size: 12px;
  color: #666;
}

/* Popup */
.popup {
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  background: #fafafa;
  box-shadow: 0 -5px 15px rgba(0, 0, 0, 0.2);
  animation: slideUp 0.3s ease-in-out;
  z-index: 20;
  border-radius: 15px 15px 0 0;
}

.popup-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 10px;
  background: #ff6347;
  background: #fff;
  color: white;
  border-radius: 10px 10px 0 0;
}

.popup-content {
  padding: 5px;
}

.close-btn {
  background: none;
  border: none;
  color: #004422;
  font-size: 22px;
  cursor: pointer;
}

@keyframes slideUp {
  from {
    transform: translateY(100%);
  }
  to {
    transform: translateY(0);
  }
}

.place-order-btn {
  width: 95%;
  padding: 8px;
  margin-top: 10px;
  background-color: #28a745;
  color: white;
  border: none;
  border-radius: 5px;
  font-size: 16px;
  font-weight: bold;
  cursor: pointer;
}

.top-header {
  position: fixed;
  top: 0;
  /* left: 0; */
  width: 100%;
  background: white;
  z-index: 10;
  padding: 10px 0;
  display: flex;
  flex-direction: column;
  align-items: center;
}

/* offer poster styles--------- */
.offer-poster {
  background-size: cover;
  background-position: center;
  width: 90%;
  position: relative;
  display: flex;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  border-radius: 10px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  overflow: hidden;
  margin: 10px;
  /* background: #fff; */
  text-align: center;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  flex: 0 0 90%;
}

.offer-content {
  text-align: center;
  color: white;
  background: rgba(0, 0, 0, 0.5);
  padding-top: 15px;
  border-radius: 10px;
}

.offer-title {
  font-size: 1.3rem;
  margin: 0;
}

.offer-description {
  font-size: 1.2rem;
  margin: 2px 0;
  width: 100%;
}

.buy-now-btn {
  padding: 7px 15px;
  background-color: #009944; /* Button color */
  border: none;
  border-radius: 5px;
  color: white;
  font-size: 1rem;
  cursor: pointer;
  margin-top: 2%;
  margin-bottom: 3%;
}

.buy-now-btn:hover {
  background-color: #004422; /* Hover effect */
}

/* TRY NEW DESIGN ---------- */
.header {
  position: fixed;
  top: 0;
  width: 100%;
  background-color: white;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 10px;
  border-bottom: 1px solid #ccc;
  z-index: 1000;
}

.search-bar-container {
  position: relative;
  flex: 1;
  display: flex;
  align-items: center;
  margin-right: 15px;
}

.search-bar {
  flex: 1;
  padding: 10px 40px 10px 40px; /* Adding padding to the left for the search icon */
  border: 1px solid #ccc;
  border-radius: 5px;
  font-size: 16px;
  color: #333;
  transition: 0.3s ease-in-out;
}

/* Adding search icon inside the input field */
.search-icon {
  position: absolute;
  left: 10px; /* Positioning the icon inside the left side of the input */
  font-size: 18px;
  color: #888;
}

/* Placeholder transition */
.search-bar::placeholder {
  transition: opacity 0.5s ease-in-out;
  opacity: 1;
}

/* Placeholder fade effect */
.search-bar:focus::placeholder {
  opacity: 0;
}
.top-filter-button {
  background-color: #009944;
  color: white;
  border: none;
  border-radius: 5px;
  padding: 5px;
  cursor: pointer;
  margin-right: 20px;
}
.button-icon {
  width: 25px; /* Adjust the size of the image */
  height: 25px; /* Adjust the size of the image */
}

.categories {
  margin-top: 70px; /* Adjust to match header height */
  display: flex;
  overflow-x: auto;
  /* gap: 10px; */
  padding: 5px;
  scrollbar-width: none;
}

.category {
  text-align: center;
}

.category-image {
  width: 50px;
  height: 50px;
  object-fit: cover;
  border-radius: 50%;
}

h2 {
  margin-top: 10px;
  padding: 0 10px;
}

.popular-items {
  display: flex;
  flex-direction: column;
  gap: 20px;
  padding: 10px;
}

.popular-item {
  display: flex;
  gap: 10px;
  align-items: center;
}

.popular-item-image {
  width: 100px;
  height: 100px;
  object-fit: cover;
  border-radius: 10px;
}

.popular-item-details h3 {
  margin: 0;
}

.popular-item-details button {
  background-color: orange;
  color: white;
  border: none;
  padding: 5px 10px;
  border-radius: 5px;
  cursor: pointer;
}
